.xui-list {
  width: 100%;
  padding-top: 15px;
}
.xui-list .xui-list-item {
  background: #fff;
  text-align: left;
  padding: 15px 15px 10px;
  margin-bottom: 15px;
}
.xui-list .xui-list-item .item-meta {
  position: relative;
  height: 28px;
}
.xui-list .xui-list-item .item-meta .icon-id {
  width: 28px;
  height: 28px;
  line-height: 28px;
  position: absolute;
  top: -6px;
  background: #1890ff;
  border-radius: 50%;
  color: #fff;
  text-align: center;
  font-size: 13px;
}
.xui-list .xui-list-item .item-meta .meta-title {
  text-indent: 38px;
  font-size: 14px;
  font-weight: bold;
}
.xui-list .xui-list-item .item-meta .area {
  height: 13px;
  min-width: 40px;
  background: #FF9800;
  color: #fff;
  border-radius: 2px;
  font-size: 12px;
  padding: 5px 10px;
  position: absolute;
  right: 30px;
  top: -2px;
}
.xui-list .xui-list-item .item-meta .location {
  height: 18px;
  position: absolute;
  right: 0;
  top: 0;
}
.xui-list .xui-list-item .item-content {
  line-height: 2;
}
.xui-list .xui-list-item .item-content .meta-line {
  display: flex;
}
.xui-list .xui-list-item .item-content .meta-line > div {
  flex: 1 1;
}
.xui-list .xui-list-item .item-content .meta-line .time {
  flex-basis: 100px;
}
.xui-list .xui-list-item .item-content p > div {
  display: inline;
  margin-right: 30px;
  font-size: 14px;
}
.xui-list .xui-list-item .item-content p.description {
  font-size: 13px;
}
.xui-list .xui-list-item .item-content .price {
  color: red;
}
.xui-list .xui-list-item.isover .meta-title {
  text-decoration: line-through;
}
@media all and (min-width: 768px) {
  .xui-list {
    background: transparent;
  }
  .xui-list .xui-list-item {
    margin-bottom: 0;
    border-bottom: 1px solid #cfcfcf;
  }
}
